<template>
  <div class="test">
    <PostHeader
      :userName="user.name"
      :userAvatar="user.avatar"
      :productName="product.name"
    />
    <div class="autoplay">
      <swiper
        :dir="'rtl'"
        :options="swiperOption"
        ref="videoSwiper"
        v-if="initOrNot"
      >
        <swiper-slide v-for="(item, index) in videoUrlListData" :key="index">
          <div class="topright">
            {{ index + 1 + "/" + videoUrlListData.length }}
          </div>
          <div @click="toggleMute"  class="bottomright_mute_icon">
            <svg
              v-if="!$store.state.muted"
              color="#ffffff"
              fill="#ffffff"
              height="12"
              role="img"
              viewBox="0 0 24 24"
              width="12"
            >
              <path
                d="M16.636 7.028a1.5 1.5 0 10-2.395 1.807 5.365 5.365 0 011.103 3.17 5.378 5.378 0 01-1.105 3.176 1.5 1.5 0 102.395 1.806 8.396 8.396 0 001.71-4.981 8.39 8.39 0 00-1.708-4.978zm3.73-2.332A1.5 1.5 0 1018.04 6.59 8.823 8.823 0 0120 12.007a8.798 8.798 0 01-1.96 5.415 1.5 1.5 0 002.326 1.894 11.672 11.672 0 002.635-7.31 11.682 11.682 0 00-2.635-7.31zm-8.963-3.613a1.001 1.001 0 00-1.082.187L5.265 6H2a1 1 0 00-1 1v10.003a1 1 0 001 1h3.265l5.01 4.682.02.021a1 1 0 001.704-.814L12.005 2a1 1 0 00-.602-.917z"
              ></path>
            </svg>
            <svg
              v-if="$store.state.muted"
              color="#ffffff"
              fill="#ffffff"
              height="12"
              role="img"
              viewBox="0 0 48 48"
              width="12"
            >
              <path
                clip-rule="evenodd"
                d="M1.5 13.3c-.8 0-1.5.7-1.5 1.5v18.4c0 .8.7 1.5 1.5 1.5h8.7l12.9 12.9c.9.9 2.5.3 2.5-1v-9.8c0-.4-.2-.8-.4-1.1l-22-22c-.3-.3-.7-.4-1.1-.4h-.6zm46.8 31.4l-5.5-5.5C44.9 36.6 48 31.4 48 24c0-11.4-7.2-17.4-7.2-17.4-.6-.6-1.6-.6-2.2 0L37.2 8c-.6.6-.6 1.6 0 2.2 0 0 5.7 5 5.7 13.8 0 5.4-2.1 9.3-3.8 11.6L35.5 32c1.1-1.7 2.3-4.4 2.3-8 0-6.8-4.1-10.3-4.1-10.3-.6-.6-1.6-.6-2.2 0l-1.4 1.4c-.6.6-.6 1.6 0 2.2 0 0 2.6 2 2.6 6.7 0 1.8-.4 3.2-.9 4.3L25.5 22V1.4c0-1.3-1.6-1.9-2.5-1L13.5 10 3.3-.3c-.6-.6-1.5-.6-2.1 0L-.2 1.1c-.6.6-.6 1.5 0 2.1L4 7.6l26.8 26.8 13.9 13.9c.6.6 1.5.6 2.1 0l1.4-1.4c.7-.6.7-1.6.1-2.2z"
                fill-rule="evenodd"
              ></path>
            </svg>
          </div>

          <video
            :muted="$store.state.muted"
            :ref="'video' + index"
            :style="
              'width:' +
              videoWidth +
              'px;height:' +
              videoWidth +
              'px;object-fit: fill'
            "
            v-if="item.type === 1"
            :src="item.url"
            @ended="endVideo(index)"
            class="multimedia"
          ></video>

          <img v-else :src="item.url" class="multimedia" />
        </swiper-slide>
        <div class="swiper-pagination" slot="pagination"></div>
      </swiper>
      <PostFooter :labels="product.labels" />
      <div class="labels">
        <template v-for="(label, index) in product.labels">
          <span class="sf-badge--number sf-badge" :key="index"
            >{{ label }}
          </span>
          &nbsp;&nbsp;
        </template>
      </div>
      <span v-if="expanded == false" class="product_description">
        {{ product.description.substr(0, 90) }}
      </span>
      <span
        v-if="expanded == false"
        @click="expand"
        class="product_description_more"
      >
        {{ "... بیشتر " }}
      </span>

      <p v-if="expanded == true" class="product_description">
        {{ product.description }}
      </p>
    </div>
  </div>
</template>

<script>
import { Swiper, SwiperSlide, directive } from "vue-awesome-swiper";
import PostHeader from "@/components/PostHeader.vue";
import PostFooter from "@/components/PostFooter.vue";
// import style (<= Swiper 5.x)
import "swiper/css/swiper.css";

export default {
  name: "AutoplayNextSwiper",
  components: {
    Swiper,
    SwiperSlide,
    PostHeader,
    PostFooter,
  },
  directives: {
    swiper: directive,
  },
  props: {
    product: {
      type: Object,
      required: true,
    },
    productIndex: { type: Number, required: true },
    focousedIndex: { type: Number, required: true },
  },

  data() {
    return {
      expanded: false,
      user: {
        avatar: require("../assets/avatar.png"),
        name: "حسین اکبری",
      },

      videoWidth: window.screen.availWidth,
      mediaLastIndex: 0,
      swiperOption: {
        speed: 1000,
        loop: false,
        observer: true,
        observeParents: true,
        autoplayDisableOnInteraction: false,
        //   allowTouchMove: false,
        pagination: {
          el: ".swiper-pagination",
          clickable: true,
          // renderBullet(index, className) {
          //   return `<span class="${className} swiper-pagination-bullet-custom"><br/>${index + 1}</span>
          // `
          // }
        },
        on: {
          slideChangeTransitionEnd: () => {
            this.slideChangeTransitionEndHandle();
          },
          slideChangeTransitionStart: () => {
            this.slideChangeTransitionStartHandle();
          },
          init: () => {
            this.initHandle();
          },
        },
      },
      initOrNot: false,
      videoUrlListData: this.product.videoUrlList,
    };
  },

  computed: {
    hasFocus: function () {
      return this.productIndex == this.focousedIndex;
    },
  },

  methods: {
    commentsClicked() {
      this.$router.push({
        name: "comments",
        params: {
          reviews: this.product.reviews,
        },
      });
    },
    expand() {
      this.expanded = true;
    },
    productClicked() {
      this.$router.push({
        name: "product",
        params: {
          productObject: this.product,
        },
      });
    },
    async initHandle() {
      if (!this.hasFocus) {
        return;
      }
      let that = this;
      setTimeout(async function () {
        let swiper = that.$refs.videoSwiper.$swiper;
        if (that.videoUrlListData[0].type === 0) {
          that.mediaNewsImgHandle(swiper);
        } else {
          await that.$refs["video" + 0][0].play();
        }
      }, 200);
    },
    mediaNewsImgHandle(swiper) {
      //刚切换到的activeIndex
      let changePointActiveIndex = swiper.activeIndex;
      if (swiper.activeIndex < this.videoUrlListData.length - 1) {
        setTimeout(function () {
          //要确认changePointActiveIndex是不是还是目前的activeIndex，是的话计时后执行，不是的话不执行
          if (changePointActiveIndex === swiper.activeIndex) {
            swiper.slideNext();
          }
        }, 2000);
      } else {
        setTimeout(function () {
          if (changePointActiveIndex === swiper.activeIndex) {
            swiper.slideTo(0, 1000);
          }
        }, 2000);
      }
    },
    slideChangeTransitionStartHandle() {
      if (!this.hasFocus) {
        return;
      }
      let swiper = this.$refs.videoSwiper.$swiper;
      if (this.videoUrlListData[this.mediaLastIndex].type === 1) {
        this.$refs["video" + this.mediaLastIndex][0].pause();
      } else {
        return;
      }
    },
    slideChangeTransitionEndHandle() {
      if (!this.hasFocus) {
        return;
      }
      this.playCurrentSlide();
      console.log("end..");
    },
    async playCurrentSlide() {
      let swiper = this.$refs.videoSwiper.$swiper;
      if (this.videoUrlListData[swiper.activeIndex].type === 0) {
        this.mediaNewsImgHandle(swiper);
      } else {
        if (this.mediaLastIndex.type === 1) {
          this.$refs["video" + this.mediaLastIndex][0].pause();
        }
        await this.$refs["video" + swiper.activeIndex][0].play();
      }
      this.mediaLastIndex = swiper.activeIndex;
      console.log(this.mediaLastIndex);
    },
    toggleMute() {
      let swiper = this.$refs.videoSwiper.$swiper;
      if (this.videoUrlListData[swiper.activeIndex].type === 1) {
        this.$store.commit("toggleMute");
        this.$refs["video" + swiper.activeIndex][0].muted =
          this.$store.state.muted;
      }
    },
    endVideo(index) {
      let swiper = this.$refs.videoSwiper.$swiper;
      if (index === swiper.activeIndex) {
        if (swiper.activeIndex < this.videoUrlListData.length - 1) {
          swiper.slideNext();
          if (this.videoUrlListData[swiper.activeIndex].type === 1) {
            this.$refs["video" + swiper.activeIndex];
          }
        } else {
          swiper.slideTo(0, 1000);
          this.$refs["video" + swiper.activeIndex];
        }
      }
    },
  },
  watch: {
    videoUrlListData: {
      handler(newName, oldName) {
        this.initOrNot = false;
        this.$nextTick(() => {
          this.initOrNot = true;
        });
      },
      immediate: true,
      deep: true,
    },
    focousedIndex: {
      handler() {
        console.log(this.productIndex + ":" + this.hasFocus);
      },
    },
    hasFocus: {
      handler() {
        console.log(
          "new has focous:  " + this.productIndex + ":" + this.hasFocus
        );

        if (this.productIndex == undefined) {
          return;
        }

        let qurySelector = '[productIndexAttr="' + this.productIndex + '"]';
        var parentElement = document.querySelectorAll(qurySelector)[0];
        var allVideos = parentElement.getElementsByTagName("video");
        for (var video of allVideos) {
          video?.pause();
        }

        if (this.hasFocus) {
          this.playCurrentSlide();
        }
      },
      deep: true,
    },
  },
};
</script>

