<template>
  <div class="home">
    <PageLayout>
      <VideoPost
        class="VideoPost"
        v-for="(product, index) in products"
        :key="index"
        :focousedIndex="focousedIndex"
        :product="product"
        :productIndex="index"
        :productIndexAttr="index"
      />
    </PageLayout>
  </div>
</template>

<script>
// @ is an alias to /src

import VideoPost from "@/components/VideoPost.vue";
import PageLayout from "@/layout/PageLayout.vue";
export default {
  name: "HomeView",
  components: {
    VideoPost,
    PageLayout,
  },
  data() {
    return {
      label: "Home",
      icon: "home",
      iconActive: "home",
   
      currentIcon: "heart",

      videoHeight: window.screen.availWidth,
      focousedIndex: 0,
    };
  },
  computed:{
      products:function(){
        return this.$store.state.products;
      }
  },
  mounted() {
    const elements = document.getElementsByClassName("VideoPost");
    const ioConfiguration = {
      /**
       * This rootMargin creates a horizontal line vertically centered
       * that will help trigger an intersection at that the very point.
       */
      rootMargin: "25% 0% -0% 0%",

      /**
       * This is the default so you could remove it.
       * I just wanted to leave it here to make it more explicit
       * as this threshold is the only one that works with the above
       * rootMargin
       */
      threshold: 1,
    };

    const observer = new IntersectionObserver(
      this.elementHasIntersected,
      ioConfiguration
    );
    for (var el of elements) {
      observer.observe(el);
    }
  },
  methods: {
    elementHasIntersected(e) {
      if (e.length > 1) {
        return;
      }
      this.focousedIndex = Number(e[0].target.getAttribute("productIndexAttr"));
      console.log(this.focousedIndex);
    },
  },
};
</script>
<style>
html,
body {
  margin: 0 !important;
}
</style>
