<template>
  <div id="product">
    <SfGallery
      :images="product.images"
      class="product__gallery"
      :image-width="350"
      :image-height="350"
      :thumb-width="160"
      :thumb-height="160"
      image-tag="nuxt-img"
      :nuxt-img-config="{
        format: 'webp',
        fit: 'cover',
      }"
      thumb-image-tag="nuxt-img"
      :thumb-nuxt-img-config="{
        format: 'webp',
        fit: 'cover',
      }"
    >
    </SfGallery>
    <div class="product">
      <div class="product__info">
        <div class="product__header">
          <SfHeading
            :title="product.name"
            :level="1"
            class="sf-heading--no-underline sf-heading--left"
          />
        </div>
        <div class="product__price-and-rating">
          <SfPrice :regular="product.price" />
          <div>
            <div class="product__rating">
              <SfRating
                :score="product.rating.rate"
                :max="product.rating.max"
              />
              <a v-if="!!product.reviews" href="#" class="product__count">
                ({{ product.reviews.length }})
              </a>
            </div>
            <SfButton
              class="sf-button--text"
              data-testid="read-all-reviews"
              @click="changeTab(2)"
            >
              خواندن نظرات
            </SfButton>
          </div>
        </div>
        <div>
          <p class="product__description desktop-only">
            {{ product.description }}
          </p>
          <SfButton class="sf-button--text desktop-only product__guide">
            Size guide
          </SfButton>
          <SfSelect
            v-model="selectedSize"
            label="اندازه"
            class="sf-select--underlined product__select-size"
            :reqired="true"
          >
            <SfSelectOption
              v-for="(size, key) in product.sizes"
              :key="key"
              :value="size"
            >
              {{ size }}
            </SfSelectOption>
          </SfSelect>
          <div class="product__colors">
            <p class="product__color-label">رنگ:</p>
            <SfColor
              v-for="(color, i) in product.colors"
              :key="i"
              :aria-label="color.name"
              :color="color.color"
              :selected="color.selected"
              class="product__color"
              @click="selectColor(i)"
            />
          </div>
          <SfAddToCart v-model="qty" class="product__add-to-cart">
            <template #add-to-cart-btn>
              <button
                @click="addToCart"
                class="sf-add-to-cart__button sf-button"
              >
                افزودن به سبد خرید
              </button>
            </template>
          </SfAddToCart>
          <SfButton class="sf-button--text desktop-only product__save">
            Save for later
          </SfButton>
          <SfButton class="sf-button--text desktop-only product__compare">
            Add to compare
          </SfButton>
        </div>
        <SfTabs
          id="tabs"
          :open-tab="openTab"
          class="product__tabs"
          @click:tab="changeTab"
        >
          <SfTab v-for="tab in tabs" :key="tab.name" :title="tab.title">
            {{ tab.content }}
            <div v-if="tab.name === 'Description'">
              <SfProperty
                v-for="(detailed, i) in product.details"
                :key="i"
                :name="detailed.name"
                :value="detailed.value"
                class="product__property"
              >
                <template v-if="detailed.name === 'Category'" #value>
                  <SfButton class="sf-button--text product__property__button">
                    {{ detailed.value }}</SfButton
                  >
                </template>
              </SfProperty>
            </div>
            <div v-else-if="tab.name === 'Read reviews'">
              <SfReview
                v-for="(review, i) in product.reviews"
                :key="i"
                :author="review.author"
                :date="review.date"
                :message="review.message"
                :max-rating="review.rating.max"
                :rating="review.rating.rate"
                :char-limit="231"
                read-more-text="Read more"
                hide-full-text="Read less"
                class="product__review"
              />
            </div>
            <div
              v-else-if="tab.name === 'Additional Information'"
              class="product__additional-info"
            >
              <p class="product__additional-info__title">برند</p>
              <p>{{ product.brand }}</p>
              <!-- <p class="product__additional-info__title">Take care of me</p>
              <p class="product__additional-info__paragraph">
                Just here for the care instructions?
              </p> -->
              <!-- <p class="product__additional-info__paragraph">
                Yeah, we thought so
              </p> -->
              <p>{{ product.careInstructions }}</p>
            </div>
          </SfTab>
        </SfTabs>
      </div>
    </div>
    <transition name="slide">
      <SfNotification
        class="notification smartphone-only"
        :visible="isOpenNotification"
        :message="`${qty} x ${product.name} (size: ${selectedSize}, color: ${selectedColor}) has been added to cart`"
        @click:close="isOpenNotification = false"
      >
        <template #icon>
          <span></span>
        </template>
      </SfNotification>
    </transition>
  </div>
</template>
<script>
import {
  SfGallery,
  SfHeading,
  SfPrice,
  SfRating,
  SfIcon,
  SfTabs,
  SfProperty,
  SfButton,
  SfReview,
  SfAddToCart,
  SfColor,
  SfSelect,
  SfBreadcrumbs,
  SfNotification,
  SfArrow,
} from "@storefront-ui/vue";

export default {
  name: "ProductComponent",
  components: {
    SfGallery,
    SfHeading,
    SfPrice,
    SfRating,
    SfIcon,
    SfTabs,
    SfProperty,
    SfButton,
    SfReview,
    SfAddToCart,
    SfColor,
    SfSelect,
    SfBreadcrumbs,
    SfNotification,
    SfArrow,
  },
  props: {
    productObject: {
      type: Object,
    },
  },
  data() {
    return {
      selectedColor: "beige",
      selectedSize: undefined,
      qty: 1,

      selected: false,
      tabs: [
        {
          name: "Description",
          title: "توضیحات",
          content: "",
        },
        {
          name: "Read reviews",
          title: "خواندن نظرات",
          content: "",
        },
        {
          name: "Additional Information",
          title: "اطلاعات تکمیلی",
          content: "",
        },
      ],
      isOpenNotification: false,
      openTab: 1,
    };
  },
  computed: {
    product: function () {
      if (!this.productObject) {
        let cartItems = this.$store.state.cartItems;
        let lastItem = cartItems[cartItems.length - 1];
        let lastProduct = this.$store.state.products.filter(
          (product) => product.id == lastItem.id
        )[0];
        return lastProduct;
      }
      console.log(this.productObject);
      return this.productObject;
    },
  },
  methods: {
    goBack() {
      this.$router.push("/");
    },
    addToCart() {
      let message = `${this.qty} x ${this.product.name} (size: ${this.selectedSize}, color: ${this.selectedColor}) has been added to cart`;

      let cartItem = {
        title: this.product.name,
        id: this.product.id,
        image: require("../assets/productA.jpg"),
        price: { regular: this.product.price },
        configuration: [
          { name: "Size", title: "اندازه", value: this.selectedSize },
          { name: "Color", title: "رنگ", value: this.selectedColor },
        ],
        qty: this.qty,
      };

      this.$store.commit("addCartItem", cartItem);

      this.$router.push("/cart");
      this.isOpenNotification = true;
      setTimeout(() => {
        this.isOpenNotification = false;
      }, 3000);
    },
    selectColor(colorIndex) {
      this.product.colors.map((el, i) => {
        if (colorIndex === i) {
          el.selected = true;
          this.selectedColor = el.name;
        } else {
          el.selected = false;
        }
      });
    },
    changeTab(tabNumber) {
      document
        .getElementById("tabs")
        .scrollIntoView({ behavior: "smooth", block: "end" });
      this.openTab = tabNumber;
    },
  },
};
</script>
